import React from 'react';
import PropTypes from 'prop-types';
import Tabs from '@mui/material/Tabs';
import Tab from '@mui/material/Tab';
import Box from '@mui/material/Box';
import Grid from '@mui/material/Unstable_Grid2';
import About from './About';
import Art from './Art';
import Research from './Research';
import Projects from './Projects';
import {styled} from '@mui/material/styles';
import './style/Menu.css';

const AntTabs = styled(Tabs)({
  '& .MuiTabs-indicator': {
    backgroundColor: '#6B9080',
  },
  'margin': 0,
});

const AntTab = styled((props) =>
  <Tab disableRipple {...props} />)(({theme}) => ({
  'textTransform': 'none',
  'minWidth': 0,
  [theme.breakpoints.up('sm')]: {
    minWidth: 0,
  },
  'fontWeight': theme.typography.fontWeightRegular,
  'padding': '1.5vw',
  'marginRight': theme.spacing(1),
  'color': 'white',
  'fontFamily': 'Lexend',
  'fontSize': '2vw',
  'minHeight': '10vw',
  '&:hover': {
    color: '#95cbb4',
    opacity: 1,
  },
  '&.Mui-selected': {
    color: '#6B9080',
  },
  '&.Mui-focusVisible': {
    backgroundColor: '#313131',
  },
}));

/**
 *
 * @param {*} props
 * @return {object}
 */
function TabPanel(props) {
  const {children, value, index, ...other} = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`vertical-tabpanel-${index}`}
      aria-labelledby={`vertical-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box sx={{p: 3}}>
          {children}
        </Box>
      )}
    </div>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.number.isRequired,
  value: PropTypes.number.isRequired,
};

/**
 *
 * @param {*} index
 * @return {object}
 */
function a11yProps(index) {
  return {
    'id': `vertical-tab-${index}`,
    'aria-controls': `vertical-tabpanel-${index}`,
  };
}

/**
 *
 * @return {object}
 */
export default function VerticalTabs() {
  const [value, setValue] = React.useState(0);

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  return (
    <Box
      sx={{flexGrow: 1, bgcolor: 'background.paper',
        display: 'flex', minHeight: 1, overflow: 'hidden'}}
    >
      <Grid className="main" container spacing={2} justify="center"
        minHeight="47vw">
        <Grid xs={2} sx={{margin: 0}}>
          <div className="spacer"></div>
          <AntTabs
            orientation="vertical"
            variant="scrollable"
            value={value}
            onChange={handleChange}
            aria-label="Vertical tabs example"
            sx={{position: 'fixed', width: '15%'}}
          >
            <AntTab className="tabs" label="About" {...a11yProps(0)}/>
            <AntTab className="tabs" label="Research" {...a11yProps(1)} />
            <AntTab className="tabs" label="Games+" {...a11yProps(2)} />
            <AntTab className="tabs" label="Art" {...a11yProps(3)} />
          </AntTabs>
        </Grid>
        <Grid className="tabContent" xs={10}>
          <TabPanel value={value} index={0}>
            <About></About>
          </TabPanel>
          <TabPanel value={value} index={1}>
            <Research></Research>
          </TabPanel>
          <TabPanel value={value} index={2}>
            <Projects></Projects>
          </TabPanel>
          <TabPanel value={value} index={3}>
            <Art></Art>
          </TabPanel>
        </Grid>
      </Grid>
    </Box>
  );
}
