import React from 'react';
import Box from '@mui/material/Box';
import ImageList from '@mui/material/ImageList';
import ImageListItem from '@mui/material/ImageListItem';

/**
 * returns a masonry type image list to display images from itemData
 * @return {object}
 */
export default function ArtImageList() {
  return (
    <Box sx={{width: 5000}}>
      <Box sx={{width: '80vw', overflowY: 'scroll', margin: 0}}>
        <ImageList xs={{margin: 0}}
          variant="masonry" cols={4} gap={8}>
          {itemData.map((item) => (
            <ImageListItem key={item.img}>
              <img
                srcSet={`${item.img}?w=248&fit=crop&auto=format&dpr=2 2x`}
                src={`${item.img}?w=248&fit=crop&auto=format`}
                alt={item.title}
                loading="lazy"
              />
            </ImageListItem>
          ))}
        </ImageList>
      </Box>
      <Box minHeight={'3vw'}></Box>
    </Box>
  );
}

