import React from 'react';
import Box from '@mui/material/Box';
import ImageList from '@mui/material/ImageList';
import ImageListItem from '@mui/material/ImageListItem';
import ImageListItemBar from '@mui/material/ImageListItemBar';
import Button from '@mui/material/Button';
import './style/Research.css';

/**
 *
 * @return {object}
 */
export default function TitlebarBelowImageList() {
  const [show, setShow] = React.useState(true);
  const moreDetails = [<DeathRebirth></DeathRebirth>,
    <TryAgain></TryAgain>];
  const [index, setIndex] = React.useState(0);

  return (
    <Box sx={{margin: 0, width: '100vw'}}>
      <Box sx={{margin: 0, width: '87vw'}}>
        {show &&
          <ImageList cols={2} gap={1}
            style={{'paddingTop': '4vw'}}>
            {itemData.map((item) => (
              <ImageListItem key={item.img}
              >
                <img
                  srcSet={`${item.img}`}
                  src={`${item.img}`}
                  alt={item.title}
                  value={item.value}
                  loading="lazy"
                  style={{'cursor': 'pointer', 'width': '80%'}}
                  onClick={(event) => displayMore(event)}
                />
                <ImageListItemBar
                  title={<span className='title'>{item.title}</span>}
                  subtitle={<span className='des'>{item.des}</span>}
                  position="below"
                />
              </ImageListItem>
            ))}
          </ImageList>
        }
        {!show &&
          <div style={{'text-align': 'center', 'width': '80vw'}}>
            {moreDetails[index]}
            <Button variant="contained" size="large" color="secondary"
              style={{'fontFamily': 'Rubik'}}
              onClick={(event) => backButton(event)}>Back</Button>
            <Box minHeight={'3vw'}></Box>
          </div>
        }
      </Box>
    </Box>
  );


  /**
   * checks with image was picked and then shows more info for it
   * @param {event} event holds information on what was inputted
   */
  function displayMore(event) {
    setShow((prev) => (!prev));
    setIndex(event.target.getAttribute('value'));
  }

  /**
   * goes back to showing all research
   * @param {event} event holds information on what was inputted
   */
  function backButton(event) {
    if (!show) {
      setShow((prev) => (!prev));
    }
  }
}

const itemData = [
  {
    img: './images/research/deathRebirth.jpg',
    title: 'Death + Rebirth',
    des: 'A Study on Player Experience and' +
      ' Failure Design through Respawning Mechanics',
    value: 0,
  },
  {
    img: './images/research/failRetry.png',
    title: 'Fail + Retry',
    des: 'A Macro-Level Taxonomy of the Challenge and Failure Process in Games',
    value: 1,
  },
];


/**
 *
 * @return {object}
 */
function DeathRebirth() {
  return (
    <Box sx={{flexGrow: 2, width: '80vw'}}>
      <img src="./images/research/jumpy.gif" alt="" width="50%"></img>
      <div style={{'text-align': 'left'}}>
        <h5>Death + Rebirth</h5>
        <h6>Abstract</h6>
        In games, failure that leads to death is a trope that players
        are all too familiar with. We were motivated by this to explore
        how altering the consequences of death on player progress
        affected aspects of the player experience. Specifically, our
        research investigated the relationship of death and respawning
        mechanics - precisely the location of respawn points - to player
        experience (PX) constructs, such as mastery, challenge, autonomy,
        curiosity, and immersion. We developed a simple 2D platformer game
        that only differed in respawn point locations: the start of the
        game (permadeath), the start of a level, the last reached
        checkpoint, and the last manually saved point. We report findings
        from a study with 72 participants that indicated modifying a
        respawn mechanic can lead to varying effects on PX and that
        different mechanics may be more effective for specific types of
        players (challenge- and goal-oriented). We then discuss the
        implications for targeted game design and opportunities for
        further research into death and respawning mechanics.
        <h6>My Contributions</h6>
        I helped code and test the platformer we made in Unity for our
        study. I also designed and created one level of the game.
        Lastly, I helped advertise our study and recruit participants,
        discussed results, and wrote a portion of the paper.
        <h6>Publication</h6>
        Marjorie Ann Cuerdo, <i>Anika Mahajan</i>, and Edward Melcer. (2021).
        <a href="https://ieeexplore.ieee.org/document/9618894"
          target="_blank" rel="noopener noreferrer"> "Die-r Consequences:
          Player Experience and the Design of Failure
          through Respawning Mechanics"</a>. In 2021 IEEE Conference on
        Games (CoG). IEEE.
      </div>
    </Box>
  );
}

/**
 *
 * @return {object}
 */
function TryAgain() {
  return (
    <Box sx={{flexGrow: 2, width: '80vw'}}>
      <img src="./images/research/failRetry.png" alt="" width="50%"></img>
      <div style={{'text-align': 'left'}}>
        <h5>Fail + Retry</h5>
        <h6>Abstract</h6>
        Player challenge is core to the player experience in video games.
         While research on challenge and failure is on the rise, the granular
         components of failure in games are relatively understudied. While
         there are existing taxonomies outlining failure and its underlying
         conditions within games, these do not account for games without
         respawning mechanics, game genres outside of platformers, or
         interaction modalities beyond traditional 2D/3D digital video games.
         To address this, we observed and coded 50 virtual reality (VR) games
         across various genres to expand upon previously developed taxonomies
         towards identifying a more generalized set of design patterns for
         player
         failure and retries. VR game genres included shooters, rhythm games,
         MMOs, story-driven puzzles, and simulations. This ensured that the new
         expanded taxonomy would uncover as many challenge and failure design
         patterns across most games. Through the process of observation and
         coding, we found that there are three main components of the failure
         process in games: (1) Player Challenges, (2) Mini Failure Loop, and
         (3) Critical Failure State. Using this taxonomy with its detailed
         sub-components could help determine how the overall structure of
         failure affects the player experience, as well as provide a new design
         language to describe challenge and the various levels of failure.
        <h6>My Contributions</h6>
        I observed and coded 20 VR games. After analyzing my codes on my own,
         my PhD mentor and I discussed our findings and created the new taxonomy
         . I also wrote part of our paper. I presented our work at the 2022
         Koret Slam and co-presented our work at IEEE COG, where our paper was
         published.
        <h6>Publication</h6>
        (In Press): Marjorie Ann Cuerdo, <i>Anika Mahajan</i>, Josh Mao, and
         Edward Melcer. (2023). <a href="https://www.researchgate.net/
        publication/372763843_Try_Again_A_Macro-Level_Taxonomy_of_the_
         Challenge_and_Failure_Process_in_Games" target="_blank"
        rel="noopener noreferrer"> "Try Again?: A Macro-Level Taxonomy of the
         Challenge and Failure Process in Games"</a>. In 2023 IEEE Conference
         on Games (CoG). IEEE.
        <h6>Poster</h6>
        <i>Anika Mahajan</i>, Edward Melcer, and Marjorie Ann Cuerdo. (2022).
        <a href="./images/research/AnikaMahajanKoretPoster.pdf"
          target="_blank" type="application/pdf"
          rel="noopener noreferrer"> "Fail and Retry: A Taxonomy of Player
         Challenge and Failure in Virtual Reality Games"</a>. Poster presented
         at: 2022 Koret Slam; May 2022; Santa Cruz, CA.
      </div>
    </Box>
  );
}
